.PostShare{
    display: flex;
    gap: 1rem;
    background-color: rgb(159 197 187 / 64%);
    padding: 1rem;
    border-radius: 1rem;
    border: 2px solid orange;
    position: absolute;
    width: 80%;
    left: 130px;
    top: 10rem;
    box-shadow: -3px 6px 9px #7d677c;
}

.PostShare>img{
    border-radius: 50%;
    width: 3rem;
    height: 3rem;
}

.PostShare>div{
    display: flex;
    flex-direction: column;
    width: 90%;
    gap: 1rem;
}

.PostShare>div>input{
    background-color: var(--inputColor);
    border-radius: 10px;
    padding: 10px;
    font-size: 17px;
    border: none;
    outline: none;
}

 



.postOptions{
    display: flex;
    justify-content: space-around;
}

.option{
    padding: 5px;
    padding-left: 10px;
    padding-right: 10px;
    border-radius: 10px;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 12px;
}

.option:hover{
    cursor: pointer;
}

.ps-button{
    padding: 5px;
    padding-left: 20px;
    padding-right: 20px;
    font-size: 12px
}


.previewImage{
    position: relative;
}

.previewImage>svg{
    position: absolute;
    right: 1rem;
    top: 0.5rem;
    cursor: pointer;
}

.previewImage>img{
    width: 100%;
    max-height: 20rem;
    object-fit: cover;
    border-radius: 0.5rem;
}
.write_about{
    outline: none;
    width: auto;
}
.newPostShare{
    position: relative;
    height: 100vh;
    width: 100vw;
    background: #ede1d2;
}