* {
	margin: 0;
	padding: 0;
	box-sizing: border-box;
	border: 0;
	/* background-color: ; */
}
:root {
	--yellow: #f5c32c;
	--orange: #fca61f;
	--black: #242d49;
	--gray: rgba(36, 45, 73, 0.65);
	--profileShadow: 0px 4px 17px 2px rgba(0, 0, 0, 0.25);
	--hrColor: #cfcdcd;
	--cardColor: rgba(255, 255, 255, 0.64);
	--buttonBg: linear-gradient(98.63deg, #f9a225 0%, #f95f35 100%);
	--inputColor: rgba(40, 52, 62, 0.07);
	--photo: #4CB256;
	--video: #4A4EB7;
	--location: #EF5757;
  --shedule: #E1AE4A;
  }
  .button{
	display: flex;
	align-items:center ;
	justify-content: center;
	color: white;
	border: none;
	border-radius: 0.5rem;
	background: var(--buttonBg);
	transition: all 100ms ease-out;
  }
  .button:hover{
	cursor: pointer;
	color: var(--orange);
	background: transparent;
	/* background-color: #4A4EB7; */
	/* border: 2px solid var(--orange); */
  }
  ::-webkit-scrollbar{
	display: none;
  }
  .button:disabled{
	background: silver;
	pointer-events: none;
  }
  .fas,.far:hover{
  cursor: pointer;
  }